import React from "react"
import styled from "styled-components"
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import {
  faBeer,
  faBiking,
  faBookOpen,
  faBrain,
  faChartArea,
  faChartBar,
  faDatabase,
  faFlask,
  faGamepad,
  faNetworkWired,
  faPaintBrush,
  faProjectDiagram,
} from "@fortawesome/free-solid-svg-icons"
import {
  faAppStoreIos,
  faBitcoin,
  faGoogleDrive,
  faGooglePlay,
  faJava,
  faJs,
  faNodeJs,
  faPython,
  faReact,
  faSuperpowers,
  faUnity,
  faWindows,
  faYoutube,
} from "@fortawesome/free-brands-svg-icons"

import Heroku from "../heroku_clean.svg"
import { faCircle } from "@fortawesome/free-regular-svg-icons"

const CentredDiv = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
`

const SourceContainer = styled.div`
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
  justify-content: center;
  align-items: center;
  justify-items: center;
  gap: 10px;
  margin: 5px;
  margin-bottom: 20px;
`

const StackContainer = styled.div`
  display: grid;
  grid-template-columns: repeat(4, auto);
  justify-content: center;
  align-items: center;
  justify-items: center;
  flex-wrap: wrap;
  gap: 8px;
`

const styles = {
  center: { display: "flex", justifyContent: "center", alignItems: "center" },
  cardHeader: {
    display: "flex",
    justifyContent: "space-between",
    alignItems: "center",
    padding: 7,
    backgroundColor: "WhiteSmoke",
  },
  card: {
    minWidth: 100,
    backgroundColor: "white",
  },
}

function StackItem({ name }) {
  const iconMap = {
    "React.js": faReact,
    Styled: faPaintBrush,
    Recharts: faChartArea,
    Axios: faProjectDiagram,
    "Node.js": faNodeJs,
    Keras: faBrain,
    Firebase: faDatabase,
    Java: faJava,
    "TCP/IP": faNetworkWired,
    Threads: faNetworkWired,
    Python: faPython,
    Pandas: faChartArea,
    SciPy: faFlask,
    "React Native": faReact,
    GraphQL: faDatabase,
    JavaScript: faJs,
    Unity: faUnity,
    ".NET C#": faWindows,
  }
  const icon = iconMap[name]
  return (
    <div key={name}>
      <CentredDiv>
        <FontAwesomeIcon icon={icon} size="lg" />
      </CentredDiv>
      {name}
    </div>
  )
}

function Stack({ stack }) {
  return (
    <StackContainer>
      {stack.map(name => (
        <StackItem name={name} key={name} />
      ))}
    </StackContainer>
  )
}

function FeatureIcon({ name }) {
  const FeatureIcons = {
    powers: faSuperpowers,
    beer: faBeer,
    bitcoin: faBitcoin,
    bike: faBiking,
    gaming: faGamepad,
    chart: faChartBar,
  }
  const FeatureIcon = FeatureIcons[name]
  return <FontAwesomeIcon icon={FeatureIcon} size="lg" />
}

function Links({ links }) {
  const icons = {
    "Source Code": () => (
      <FontAwesomeIcon
        icon={faCircle}
        color="black"
        size="lg"
        style={{ marginRight: 5 }}
      />
    ),
    Demo: () => (
      <img
        alt="Heroku icon"
        src={Heroku}
        style={{ height: 20, margin: 0, marginRight: 5 }}
      />
    ),
    Notebook: () => (
      <FontAwesomeIcon
        icon={faBookOpen}
        color="tomato"
        size="lg"
        style={{ marginRight: 5 }}
      />
    ),
    Paper: () => (
      <FontAwesomeIcon
        icon={faGoogleDrive}
        size="lg"
        color="#00abbf"
        style={{ marginRight: 5 }}
      />
    ),
    "iOS App Store": () => (
      <FontAwesomeIcon
        icon={faAppStoreIos}
        size="lg"
        color="#00abff"
        style={{ marginRight: 5 }}
      />
    ),
    "Play Store": () => (
      <FontAwesomeIcon
        icon={faGooglePlay}
        size="lg"
        color="#00abbf"
        style={{ marginRight: 5 }}
      />
    ),
    YouTube: () => (
      <FontAwesomeIcon
        icon={faYoutube}
        size="lg"
        color="red"
        style={{ marginRight: 5 }}
      />
    ),
  }

  return (
    <SourceContainer>
      {links.map(({ link, type }) => {
        const Icon = icons[type]
        return (
          <a target="_blank" rel="noopener noreferrer" href={link} key={link}>
            <Icon />
            {type}
          </a>
        )
      })}
    </SourceContainer>
  )
}

export default function BasicCard(props) {
  const { stack, links, feature, title, description } = props

  return (
    <div style={styles.card}>
      <div style={styles.cardHeader}>
        <Stack stack={stack} />
        <FeatureIcon name={feature} />
      </div>
      <div style={{ padding: 20 }}>
        <h5>{title}</h5>
        <div>{description}</div>
      </div>
      <Links links={links} />
    </div>
  )
}
